.scanlines-container {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    max-height: 100vh;
    height: 1080px;
    .scanlines {
        height: 100%;
        overflow: hidden;
        mix-blend-mode: difference;
        z-index: 1;

        &::before {
            pointer-events: none;
            
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            z-index: 2;
    
            width: 100%;
            max-height: 100vh;
            height: 1080px;
            
            top: 0;
            left: 0;
            
            background-image: repeating-linear-gradient(
                to bottom,
                transparent 0%,
                    rgba(255, 255, 255, 0.1) 1%,
                transparent 1%,
            );
        }

        &::after {
            pointer-events: none;
            
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            z-index: 2;
    
            width: 100%;
            max-height: 100vh;
            height: 1080px;
            
            background-image: linear-gradient(
                to top,
                rgba(14,14,14, 0.5) 25%,
                rgba(0,0,0, 0) 100%,
            );
        }
    }
}