@import '../../styles/defaults.scss';

.peach {
    $OrbStop1: #ff6cfc;
    $OrbStop2: #ffa15e;
    
    .firstStop {
        stop-color: $OrbStop1;
    }
    .secondStop {
        stop-color: $OrbStop2;
    }
}

.bubblegum {
    $OrbStop1: #ca76fd;
    $OrbStop2: #9efdff;
    .firstStop {
        stop-color: $OrbStop2;
    }
    .secondStop {
        stop-color: $OrbStop1;
    }
}

.speedydrink {
    $OrbStop1: #fbff56;
    $OrbStop2: #74d8ff;
    .firstStop {
        stop-color: $OrbStop1;
    }
    .secondStop {
        stop-color: $OrbStop2;
    }
}