@import '../../styles/defaults.scss';

.pictureElement-wrapper {
	position: absolute;
	z-index: 0;
	left: 0;
	top: 100%;
    width: 100vw;
    height: 100vh;
    overflow-y:visible;
	.pictureElementWire {
        width: 100%;
        height: 100%;
        position: absolute;
        top:0%;
        z-index: 0;
        
        background-image: url('./home_wire_landscape_v3.png');
        background-attachment: scroll;
		background-size: auto;
		background-repeat: no-repeat;
        filter: saturate(1.5) drop-shadow(0 0 1.5rem $dominant) drop-shadow(0 0 2.5rem darkblue);
    }
    .pictureElementGradient{
        position: absolute;
        z-index: 1;
        left:0;
        height: 100vh;
        width: 100%;
        top: -5%;
        background: none;
        background: -moz-linear-gradient(top,  rgba(14,14,14,0) 45%,rgba(14,14,14,0.8) 93%, rgba(14,14,14,1) 100%);
        background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(14,14,14,0)), color-stop(93%,rgba(14,14,14,1)), color-stop(100%, rgba(14,14,14,1)));
        background: -webkit-linear-gradient(bottom,  rgba(14,14,14,0) 45%,rgba(14,14,14,0.8) 93%, rgba(14,14,14,1) 100%);
        background: -o-linear-gradient(bottom,  rgba(14,14,14,0) 45%,rgba(14,14,14,0.8) 93%, rgba(14,14,14,1) 100%);
        background: -ms-linear-gradient(bottom,  rgba(14,14,14,0) 45%,rgba(14,14,14,0.8) 93%, rgba(14,14,14,1) 100%);
        background: linear-gradient(to top,  rgba(14,14,14,0) 45%,rgba(14,14,14,1) 93%, rgba(14,14,14,1) 100%);
       filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0e0e0e', endColorstr='#ffffff',GradientType=0 );
    }
}
