@import '../../styles/defaults.scss';

$orbHeadingTextColour: $text;

.orbheading-wrapper{
    height: 90vh;
}
.orbHeading-container{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
    max-height: 100vh;
    .orbHeading-child:nth-child(1) {
        z-index: 1;
        position: absolute;
        //padding: 0 25vw 0 25vw;
    }
    .orbHeading-child:nth-child(2) {
        z-index: 0;
        position: absolute;
        //padding: 0 2 0 25vw;
    }
}


.orbHeading-headerText {
    z-index: 2;
    color: $orbHeadingTextColour;
    font-family: 'vcrfont';
    font-size: 100px;
    font-weight: normal;
    font-kerning: .5rem;

    @media (max-height: 784) {
        z-index: 2;
        color: $orbHeadingTextColour;
        font-family: 'vcrfont';
        font-size: 80px;
        font-weight: normal;
        font-kerning: .4rem;
    }
}

